import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { AuthService } from '../services/auth.service';
import { Router } from '@angular/router';
import { take } from 'rxjs/operators';


@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {
  loginClicked = false;
  errorOccurred = false;

  constructor(
    private authService: AuthService,
    private router: Router) {
  }

  ngOnInit(): void {
    this.authService.isJwtTokenValid.pipe(take(1))
      .subscribe(data => {
        if (data) {
          this.router.navigateByUrl('/wygeneruj-fakture');
        }
      });
  }

  loginJwtToken(loginForm: NgForm): void {
    if (!loginForm.valid) {
      return;
    }
    if (!this.loginClicked) {
      this.loginClicked = true;
    }
    const username = loginForm.value.email;
    const password = loginForm.value.password;
    this.authService.loginJwtToken(username, password).subscribe(
      () => {
        this.router.navigateByUrl('/wygeneruj-fakture');
      },
      () => {
        this.errorOccurred = true;
        this.loginClicked = false;
      });
  }
}
