import { Injectable } from '@angular/core';
import * as moment from 'moment';
import * as pdfMake from 'pdfmake/build/pdfmake';
import * as pdfFonts from 'pdfmake/build/vfs_fonts';
pdfMake.vfs = pdfFonts.pdfMake.vfs;

interface ExportData {
  content: Array<object>;
  styles: {};
}

@Injectable({
  providedIn: 'root'
})
export class PdfService {
  invoiceNumber: string;
  currentDate: string;
  firstDayOfMonth: string;
  lastDayOfMonth: string;
  currentMonth: string;

  constructor() {
    moment.locale('pl');
    this.currentMonth = moment().format('MMMM YYYY');
    this.currentDate = moment().format('DD.MM.YYYY');
    this.firstDayOfMonth = moment().startOf('month').format('DD.MM.YYYY');
    this.lastDayOfMonth = moment().endOf('month').format('DD.MM.YYYY');
    this.invoiceNumber = moment().format('M/YYYY');
  }

  saveDoc(data): void {
    const doc = this.prepareDoc(data);
    pdfMake.createPdf(doc).download(`Rachunek ${this.currentMonth} ${data.docName}.pdf`);
  }

  prepareDoc(data): ExportData {
    return {
      content: [
        {text: `${data.city}, dnia ${this.currentDate}`, style: 'headerDate'},
        {text: '(miejscowość)                  (data wystawienia)', style: 'headerSmallRight'},
        {
          columns: [
            {
              width: '*',
              text: 'Sprzedawca'
            },
            {
              width: '*',
              text: 'Nabywca'
            }
          ], margin: [0, 40, 0, 0]
        },
        {
          columns: [
            {
              width: '*',
              text: data.sellerName
            },
            {
              width: '*',
              text: data.buyerName
            }
          ], margin: [0, 15, 0, 0]
        },
        {
          columns: [
            {
              width: '*',
              text: data.sellerAddress
            },
            {
              width: '*',
              text: data.buyerNip
            }
          ], margin: [0, 10, 0, 0]
        },
        {
          columns: [
            {
              width: '*',
              text: ''
            },
            {
              width: '*',
              text: data.buyerAddress
            }
          ], margin: [0, 10, 0, 0]
        },
        {text: `RACHUNEK NR ${this.invoiceNumber}`, style: 'invoiceNumber', margin: [0, 70, 0, 0]},
        {
          columns: [
            {
              width: '*',
              text: this.currentDate
            },
            {
              width: '*',
              text: ''
            }
          ], margin: [0, 40, 0, 0]
        },
        {
          columns: [
            {
              width: '*',
              text: '(data sprzedaży/wykonania usługi)',
              style: 'headerSmall'
            },
            {
              width: '*',
              text: ''
            },
          ]
        },
        {text: 'Sposób płatności: Przelew na konto', margin: [0, 15, 0, 0]},
        {text: 'Termin płatności: 28 dni', margin: [0, 5, 0, 0]},
        {text: `Nazwa Banku: ${data.bankName}.`, margin: [0, 5, 0, 0]},
        {text: `Numer konta: ${data.bankNumber}`, margin: [0, 5, 0, 0]},
        {
          style: 'invoiceNumber',
          margin: [0, 50, 0, 0],
          table: {
            // headers are automatically repeated if the table spans over multiple pages
            // you can declare how many rows should be treated as headers
            headerRows: 1,
            widths: [20, '*', 70, 100, 70],

            body: [
              ['Lp.', 'Nazwa usługi', 'Ilość', 'Cena jednostkowa', 'Wartość'],
              ['1',
                `${data.serviceName} ${this.firstDayOfMonth} - ${this.lastDayOfMonth}`,
                '1', `${data.price}.00 zł`, `${data.price}.00 zł`],
            ]
          }
        },
        {text: `Do zapłaty: ${data.price}.00 zł`, margin: [0, 15, 0, 0]},
        {text: `(słownie: ${data.priceString})`, margin: [0, 5, 0, 0]}
      ],


      styles: {
        headerDate: {
          alignment: 'right'
        },
        headerSmallRight: {
          alignment: 'right',
          fontSize: 8,
          italics: true
        },
        headerSmall: {
          fontSize: 8,
          italics: true
        },
        invoiceNumber: {
          alignment: 'center',
        }

      }

    };
  }
}
