import { Injectable } from '@angular/core';
import { TokenData } from '../models/token-data.model';

@Injectable({
  providedIn: 'root'
})
export class TokenDataService {

  TOKEN_KEY = 'auth-token';

  saveTokenData(token: TokenData): void {
    localStorage.removeItem(this.TOKEN_KEY);
    localStorage.setItem(this.TOKEN_KEY, JSON.stringify(token));
  }

  getTokenData(): TokenData {
    return JSON.parse(localStorage.getItem(this.TOKEN_KEY));
  }

  removeTokenData(): void {
    localStorage.removeItem(this.TOKEN_KEY);
  }

  validateToken(authData: TokenData): boolean {
    return authData && authData.token ? new Date() <= new Date(authData.exp * 1000) : false;
  }
}
