import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import * as moment from 'moment';
import { PdfService } from '../services/pdf.service';
import { DataService } from '../services/data.service';

@Component({
  selector: 'app-generator',
  templateUrl: './generator.component.html'
})
export class GeneratorComponent implements OnInit {
  currentMonth: string;
  isLoggedIn = true;
  @ViewChild('downloadButton') downloadButtonRef: ElementRef;

  constructor(private pdfService: PdfService,
              private dataService: DataService) {
  }

  ngOnInit(): void {
    moment.locale('pl');
    this.currentMonth = moment().format('MMMM YYYY');
  }

  downloadPDF(): void {
    this.downloadButtonRef.nativeElement.blur();
    this.dataService.getInvoiceData()
      .subscribe(data => data ? this.pdfService.saveDoc(data) : this.isLoggedIn = !!data);
  }
}
