import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { combineLatest, Observable, of } from 'rxjs';
import { AuthService } from './auth.service';
import { switchMap, take } from 'rxjs/operators';
import { environment } from '../../environments/environment';

@Injectable({
  providedIn: 'root'
})
export class DataService {

  constructor(private httpClient: HttpClient,
              private authService: AuthService) {
  }

  getInvoiceData(): Observable<any> {
    return combineLatest([this.authService.jwtToken, this.authService.isJwtTokenValid]).pipe(
      take(1),
      switchMap(([jwtToken, jwtTokenValidity]) => {
        return jwtTokenValidity ? this.httpClient.get(environment.url + '/inpost', {
          headers: new HttpHeaders({Authorization: `Bearer ${jwtToken}`})
        }) : of(null);
      }),
    );
  }
}
